<template>
  <div class="coinBg" :style="bgStyle"></div>
</template>

<style>
.coinBg {
  position: fixed;
  width: 98%;
  height: 92%;
  background-size: cover;
  background-position: bottom right;
  background-clip: border-box;
  background-repeat: no-repeat;
  background-size: 25%;
  filter: blur(5px) brightness(0.5) drop-shadow(16px 16px 16px);
  z-index: -1;
  animation: fadeSlide 2s;
}

@keyframes fadeSlide {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>

<script>
export default {
  props: {
    bgImage: { required: true },
  },
  computed: {
    bgStyle() {
      return 'background-image: url("' + this.bgImage + '")';
    }
  },
};
</script>