<template>
    <meta name="description" :content="`Cryptocurrency information page for ${ $route.name.toUpperCase() }`" />
    <PageBg :bgImage="`${require(`../assets/logos/${$route.name}.png`)}`" />
    <div class="container">
        <div class="row">
            <div class="col-md-6 offset-md-3">
                <h1 class="text-center mt-5 display-1 brand-white">{{ $route.name.toUpperCase() }}</h1>
                <div v-if="$route.name === 'btc'" role="region" aria-label="Bitcoin Information">
                    <p class="text-secondary">
                        <strong class="text-body">Bitcoin,</strong> created by the mysterious Satoshi Nakamoto, launched in
                        2009 as the first decentralized
                        digital currency. It operates on a secure blockchain, allowing peer-to-peer transactions without
                        intermediaries. Bitcoin's scarcity (only 21 million coins) and store-of-value narrative attract
                        investors worldwide.

                    </p>
                </div>
                <div v-if="$route.name === 'eth'" role="region" aria-label="Ethereum Information">
                    <p class="text-secondary">
                        <strong class="text-body">Etherum,</strong> founded by Vitalik Buterin, introduced smart contracts
                        and decentralized applications (DApps). Its native cryptocurrency, Ether (ETH), powers the network.
                        Ethereum's versatility extends beyond currency, enabling DeFi, NFTs, and more.
                    </p>
                </div>
                <div v-if="$route.name === 'doge'" role="region" aria-label="Dogecoin Information">
                    <p class="text-secondary">
                        <strong class="text-body">Dogecoin,</strong> began as a playful joke, featuring the Shiba Inu dog
                        meme. Despite its origins, it gained a loyal community. Dogecoin is used for tipping and embodies a
                        fun spirit within the crypto space.

                    </p>
                </div>
                <div v-if="$route.name === 'ltc'" role="region" aria-label="Litecoin Information">
                    <p class="text-secondary">
                        <strong class="text-body">Litecoin,</strong> created by Charles “Charlie” Lee in 2011, is often
                        dubbed “the silver to Bitcoin's gold.” It shares Bitcoin's open-source code but differentiates
                        itself with faster block generation (2.5 minutes) and the Scrypt hashing algorithm. Litecoin serves
                        as a reliable cryptocurrency for everyday transactions, emphasizing speed, lower fees, and
                        decentralization.

                    </p>
                </div>
                <div class="card border-primary m-3" style="min-width: 20rem">
                    <div class="card-body">
                        <div class="lead mt-3">Buy {{ $route.name.toUpperCase() }}</div>
                        <div class="card-body">
                            <div>
                                <div class="input-group mb-3">
                                    <input type="text" class="form-control" v-model="buyInputValue"
                                        aria-label="Amount to buy">
                                    <span class="input-group-text">{{ $route.name.toUpperCase() }}</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-between align-items-center">
                                <button class="btn btn-primary" onclick="alert('Buying is not implemented :)')"
                                    aria-label="Buy button">Buy</button>
                                <p class="lead" aria-label="Total price">
                                    {{ buyPrice.toFixed(2) }} $
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="display-5">Current Price:</div>
                <div class="display-5">1 {{ $route.name.toUpperCase() }} = {{ currentPrice.toFixed(2) }} $</div>
                <div id="chart" class="w-100" role="region" aria-label="Price Chart"></div>
            </div>
        </div>
    </div>
</template>

<script>
import ApexCharts from 'apexcharts';
import PageBg from '@/components/PageBg.vue';


export default {
    name: 'CoinPage',
    components: {
        PageBg,
    },
    data() {
        return {
            chart: null,
            currentPrice: 0,
            buyInputValue: 0,
        };
    },
    watch: {
        '$route': {
            immediate: true,
            handler() {
                this.loadChartData();
            },
        },
    },
    computed: {
        buyPrice() {
            return this.buyInputValue * this.currentPrice;
        },
    },
    methods: {
        loadChartData() {
            fetch(`/fakeapi/${this.$route.name.toLowerCase()}.json`)
                .then(response => response.json())
                .then(data => {
                    if (this.chart) {
                        this.chart.destroy();
                    }
                    this.currentPrice = data[0].y[3];
                    data.reverse() // this should never be neccessary but the data is not sorted correctly


                    const options = {
                        series: [{
                            data: data.map(item => ({
                                x: new Date(item.x).toLocaleString(),
                                y: [item.y[0], item.y[1], item.y[2], item.y[3]]
                            }))
                        }],
                        chart: {
                            type: 'candlestick',
                            height: 350,
                            width: '100%'
                        },
                        responsive: [{
                            breakpoint: undefined,
                            options: {},
                        }],
                        tooltip: {
                            enabled: true,
                            style: {
                                color: '#fff',
                            }
                        },
                    };
                    this.chart = new ApexCharts(document.querySelector('#chart'), options);
                    this.chart.render();
                })
                .catch(error => {
                    console.error('Error loading chart data:', error);
                });
        }
    }
}
</script>

<style>
.apexcharts-xaxistooltip-text,
.apexcharts-menu-item {
    color: var(--bs-body-color) !important;
}

.apexcharts-menu-item:hover {
    background-color: rgba(255, 255, 255, 0.15) !important;
}

.apexcharts-menu-item:active {
    background-color: #ea39b8 !important;
}

.apexcharts-tooltip,
.apexcharts-xaxistooltip,
.apexcharts-menu {
    background: var(--bs-body-bg) !important;
}

.apexcharts-xaxis-label,
.apexcharts-yaxis-label {
    fill: #ea39b8 !important;
    text-shadow: 0 0 1px rgba(234, 57, 184, 0.3), 0 0 2px rgba(234, 57, 184, 0.3), 0 0 5px rgba(234, 57, 184, 0.2);
}
</style>


