<template>
    <nav class="navbar bg-dark" aria-label="breadcrumb">
        <div class="container-fluid">
            <ol class="breadcrumb">
                <li class="breadcrumb-item nav-link">
                    <router-link to="/">HOME</router-link>
                </li>
                <li class="breadcrumb-item nav-link" v-for="(crumb, index) in breadcrumbs" :key="index">
                    <router-link :to="crumb.url">{{ crumb.label }}</router-link>
                </li>
            </ol>
        </div>
    </nav>
</template>

<script>
export default {
    data() {
        return {
            breadcrumbs: []
        };
    },
    mounted() {
        this.generateBreadcrumbs();
    },
    watch: {
        $route() {
            this.generateBreadcrumbs();
        }
    },
    methods: {
        generateBreadcrumbs() {
            const path = window.location.pathname;
            const segments = path.split('/').filter(segment => segment !== '');

            this.breadcrumbs = segments.map((segment, index) => {
                const url = `/${segments.slice(0, index + 1).join('/')}`;
                const label = segment.toUpperCase();

                return { url, label };
            });
        }
    }
};
</script>
