import { createRouter, createWebHistory } from 'vue-router'

import CoinPage from '@/views/CoinPage.vue'

const routes = [
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/MainPage.vue')
    },
    {
        path: '/about',
        name: 'about',
        component: () => import('@/views/AboutPage.vue')
    },
    {
        path: '/coin/btc',
        name: 'btc',
        component: CoinPage,
    },
    {
        path: '/coin/eth',
        name: 'eth',
        component: CoinPage,
    },
    {
        path: '/coin/ltc',
        name: 'ltc',
        component: CoinPage,
    },
    {
        path: '/coin/doge',
        name: 'doge',
        component: CoinPage,
    },
    {
        path: '/coin',
        name: 'allcoins',
        component: () => import('@/views/CoinPageAll.vue')
    },
    {
        path: '/nft',
        name: 'nft',
        component: () => import('@/views/NFTCardGallery.vue')
    },
    {
        path: '/learn/chain',
        name: 'learn-chain',
        component: () => import('@/views/LearnBlockchain.vue')
    },
    {
        path: '/learn/nfts',
        name: 'learn-nfts',
        component: () => import('@/views/LearnNFTs.vue')
    },
    {
        path: '/learn',
        name: 'learn',
        redirect: '/'
    }
]

const router = createRouter({
    history: createWebHistory(),
    routes: routes.map(route => ({ ...route, name: route.name.toLowerCase() }))
})

export default router